<template>
    <div>
        <el-form ref="form" :model="form" label-width="130px" :rules="rules">
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="选择车型" prop="car_brand">
                        <el-input v-model="form.car_brand" v-show="false"></el-input>
                        <el-button @click="handleSelectCarType" :disabled="isDisabled()">车型选择</el-button>
                    </el-form-item>
                    <el-form-item label="车辆品牌">
                        {{form.car_brand}}
                    </el-form-item>
                    <el-form-item label="车辆系列">
                        {{form.car_series}}
                    </el-form-item>
                    <el-form-item label="车辆类型">
                        {{form.car_class}}
                    </el-form-item>
                    <el-form-item label="公告型号" prop="foreign_model">
                        <el-input v-model="form.foreign_model" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="燃油类型" prop="fuel_type">
                        <el-select v-model="form.fuel_type" placeholder="请选择燃油类型" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsFuel"
                                :key="item.id"
                                :label="item.type_item_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="马力" prop="horse_power">
                        <el-input v-model="form.horse_power" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="驱动形式" prop="drive_form">
                        <el-select v-model="form.drive_form" placeholder="请选择驱动形式" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsDrive"
                                :key="item.id"
                                :label="item.type_item_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="颜色" prop="color">
                        <el-input v-model="form.color" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="采购单价" prop="purchase_price">
                        <el-input v-model="form.purchase_price" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="销售单价" prop="sale_price">
                        <el-input v-model="form.sale_price" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>

        <!-- 选择车型 -->
        <el-dialog title="选择车型" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar v-if="hackReset" :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    import { requestUrl } from '@/api/config'
    import selectCar from '@/components/selectCar'
    export default {
        data: function(){
            return {
                collapseActive: ['1'],
                optionsFuel:[],
                optionsDrive:[],
                hackReset: false,
                dialogSelectCarType: false,
                pageType: '',
                selectCarOptions:{
                    tableConfig:{
                        bus_brand: true,
                        bus_type: true,
                        bus_series: true
                    },
                    formConfig: {
                        
                    },
                    pageType: 'carTypeSelect'
                    
                },
                form: {
                    select_car_type:'',
                    car_brand: "",
                    car_class: "",
                    car_series: "",
                    color: "",
                    drive_form: "",
                    foreign_model: "",
                    fuel_type: "",
                    horse_power: "",
                    purchase_price: "",
                    sale_price: ""
                },
                rules: {
                    car_brand:[
                        { required: true, message: '请选择车型', trigger: 'change' }
                    ],
                    foreign_model:[
                        { required: true, message: '请输入公告型号', trigger: 'blur' }
                    ],
                    fuel_type:[
                        { required: true, message: '请选择燃油类型', trigger: 'change' }
                    ],
                    horse_power:[
                        { required: true, message: '请输入马力', trigger: 'blur' }
                    ],
                    drive_form:[
                        { required: true, message: '请选择驱动形式', trigger: 'change' }
                    ],
                    color:[
                        { required: true, message: '请输入颜色', trigger: 'blur' }
                    ],
                    purchase_price:[
                        { required: true, message: '请输入采购单价', trigger: 'blur' }
                    ],
                    sale_price:[
                        { required: true, message: '请输入销售单价', trigger: 'blur' }
                    ]
                }
            }
        },
        components:{
            selectCar
        },
        mounted(){
            this.checkPath()
            this.getFuel()
            this.getDrive()
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData();
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.pageType = 'view';
                    this.getFormData();
                }
            },
            getFormData(){
                this.form.id = this.$route.query.id;
                this.form.operation_uniqid = this.$route.query.operation_uniqid;
                API.apiGoodsParamsView({id: this.$route.query.id, operation_uniqid: this.$route.query.operation_uniqid}).then(res => {
                    Object.assign(this.form,res.data)
                })
            },
            getFuel(){
                API.apiGetFuelTypes({}).then(res => {
                    this.optionsFuel = res.data
                })
            },
            getDrive(){
                API.apiGetDriveTypes({}).then(res => {
                    this.optionsDrive = res.data
                })
            },
            handleSelectCarType(){
                this.dialogSelectCarType = true
                this.hackReset = false
                this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            handleSubmit() {
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        if(this.pageType == 'add'){
                            API.apiGoodsParamsAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }else{
                            API.apiGoodsParamsEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }
                    }else{
                        return false
                    }
                })
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                this.form.car_brand = row.bus_brand
                this.form.car_series = row.bus_series
                this.form.car_class = row.bus_type
                this.dialogSelectCarType = false;
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>